import * as React from "react";
import ReactDOM from "react-dom";
import { useForm, SubmitHandler } from "react-hook-form";
import Layout from "../components/layout/Layout";
import Container from "react-bootstrap/Container";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { Link, navigate } from "gatsby";
import { runCloudFunction } from "../vendor/ParseApp";
import Toast from "react-bootstrap/Toast";
import Tooltip from "react-bootstrap/Tooltip";
import Badge from "react-bootstrap/Badge";
enum GenderEnum {
  female = "female",
  male = "male",
  other = "other",
}

interface IFormInput {
  firstName: String;
  gender: GenderEnum;
}

export default function App() {
  const { register, handleSubmit } = useForm<IFormInput>();
  const onSubmit: SubmitHandler<IFormInput> = (data) => console.log(data);

  const [message, setMessage] = React.useState("");
  const [model, setModel] = React.useState({
    type: 0,
  });

  const setModelValue = (name, value) => {
    setModel({
      ...model,
      [name]: value,
    });
  };

  return (
    <Layout>
      <Container className="">
        <Form>
          <Row>
            <Col>
              <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Label>First Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder=""
                  onChange={(v) => setModelValue("name", v.target.value)}
                />
              </Form.Group>
            </Col>
            <Col>
              <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Label>Family Name</Form.Label>
                <Form.Control
                  type="text"
                  placeholder=""
                  onChange={(v) => setModelValue("familyName", v.target.value)}
                />
              </Form.Group>
            </Col>
          </Row>

          <Row>
            <Col xs="12" md="6">
              <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Label>Email Address</Form.Label>
                <Form.Control
                  type="text"
                  placeholder=""
                  onChange={(v) => setModelValue("email", v.target.value)}
                />
              </Form.Group>
            </Col>
            <Col xs="12" md="6">
              <Form.Group
                className="mb-3"
                controlId="exampleForm.ControlInput1"
              >
                <Form.Label>Password</Form.Label>
                <Form.Control
                  type="password"
                  placeholder=""
                  onChange={(v) => setModelValue("password", v.target.value)}
                />
              </Form.Group>
            </Col>
          </Row>

          <h4>
            <Badge
              bg={`${model.type === 0 ? "success" : "secondary"} defaultCursor`}
              onClick={() => setModelValue("type", 0)}
            >
              {" "}
              Job Seeker
            </Badge>{" "}
            <Badge
              onClick={() => setModelValue("type", 1)}
              bg={`${model.type === 1 ? "success" : "secondary"} defaultCursor`}
            >
              Recruiter
            </Badge>
          </h4>

          <Button
            variant="primary"
            onClick={() => {
              runCloudFunction("createUser", model)
                .then((e) => {
                  setMessage("Please verify your email address");
                })
                .catch((e) => {
                  setMessage(e?.message);
                });
            }}
          >
            Create Account
          </Button>
        </Form>
        <div>{message}</div>
      </Container>
    </Layout>
  );
}
